import React, {useEffect} from "react";
import mixitup from "mixitup";
import "./product-cards.scss"
import ProductCard from "../product-card/product-card";

const productCards = [
  {
    id: 1,
    description: "   ",
    imageName: "cheese",
    price: 160,
    category: "burger"
  },
  {
    id: 2,
    description: "",
    imageName: "beef",
    price: 190,
    category: "pizza"
  },
  {
    id: 3,
    description: " ",
    imageName: "queen",
    price: 260,
    category: "sandwich"
  },
  {
    id: 4,
    description: " ",
    imageName: "black",
    price: 199,
    category: "asia"
  },
  {
    id: 5,
    description: " ",
    imageName: "chicken",
    price: 180,
    category: "set"
  },
];

const ProductCards = () => {
  useEffect(() => {
    mixitup(".product-cards");
  }, []);

  return <ul className="product-cards">
    {productCards.map(card => <li key={card.id} className={`mix ${card.category}`}>
      <ProductCard {...card} />
    </li>)}
    {productCards.map(card => <li key={card.id} className={`mix ${card.category}`}>
      <ProductCard {...card} />
    </li>)}
  </ul>;
}

export default ProductCards;