import React from "react";
import "./buttons-burger.scss"
import ButtonBurger from "../button-burger/button-burger";

const burgerButtons = [ //todo 
  {
    id: 1,
    burgerName: "",
    imageName: "burger"
  },
  {
    id: 2,
    burgerName: "",
    imageName: "pizza"
  },
  {
    id: 3,
    burgerName: "",
    imageName: "sandwich"
  },
  {
    id: 4,
    burgerName: " ",
    imageName: "asia"
  },
  {
    id: 5,
    burgerName: "",
    imageName: "set"
  }
];

const ButtonsBurger = () => {
  return <ul className="buttons-burger">
    {burgerButtons.map(burger => <li key={burger.id}>
      <ButtonBurger {...burger} />
    </li>)}
  </ul>;
}

export default ButtonsBurger;